.box-drawer {
  position: absolute;
  bottom: 100%;
  left: 0;
  width: 100%;
  transition: 0.40s ease;
  z-index: 1;
  background: #EEEEEE;
  color: #b20202;
  border-top: 12px solid #b20202;
  text-align: center;
  padding: 1rem; }
  .box-content-drawer:focus .box-drawer,
  .box-content-drawer:hover .box-drawer {
    bottom: 0; }
    .box-content-drawer:focus .box-drawer .box-handle,
    .box-content-drawer:hover .box-drawer .box-handle {
      display: none; }
  .box-drawer h3 {
    font-size: calc(20px (46 - 20) * ((100vw - 320px) / (640 - 320)));
    margin-bottom: 0.9375rem; }
  .box-drawer ul {
    margin-bottom: 0;
    font-size: calc(16px (21 - 16) * ((100vw - 320px) / (640 - 320))); }
  .box-drawer .contentdrawer {
    margin: 0 0 1rem; }

.box-text {
  position: relative;
  z-index: 3; }

.box-handle {
  position: absolute;
  bottom: 0;
  width: 5rem;
  left: 50%;
  transform: translateX(-50%) translateY(40%);
  transition: top 0.15s;
  text-align: center;
  background: #EEEEEE;
  color: #b20202;
  padding: 4rem 0 0;
  z-index: 2;
  font-weight: 400; }
  .box-handle .symbol {
    fill: #b20202;
    width: 1.875rem;
    height: 2rem; }

